.header__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.header__logout{
  font-weight: 900;
  font-size: 32px;
  line-height: 44px;
}
.header__logout a{
  color: black;
}
.pop-up {
  height: 100vh;
  width: 100vw;
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.pop-up__close{
  position: fixed;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
.pop-up__form{
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.pop-up__content {
  background-color: white;
  padding: 27px 20px;
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.pop-up__content div button{
  margin: 20px 0 0 0;
}
.item__container{
  width: 80%;
  margin: 20px 0;
  padding: 15px;
  display: flex;
  position: relative;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border: 3px solid rgba(186, 186, 186, 0.4);
}
.pop-up__input {
  padding: 14px 53px 14px 10px;
  border-radius: 10px;
}
.pop-up__content input + input {
  margin: 20px 0;
}
.pop-up__title {
  font-weight: 900;
  font-size: 32px;
  line-height: 44px;
  margin: 0 0 10px 0;
}
.header__container--space{
  justify-content: space-between;
}


@media (min-width: 800px){
  .header__btn-list{
    display: flex;
  }
  .header__btn-list div + div{
    margin: 0 20px;
  }
  .pop-up__input {
    padding: 14px 137px 14px 10px;
    border-radius: 10px;
  }
  .item__container{
    margin: 30px 20px 30px 0;
    max-width: 300px;
  }
}
@media (max-width: 800px){
  .header__btn-list{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
  .header__container{
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .header__btn{
    margin: 20px 0;
  }
}